:root {
  /* BASE COLORS */
  --color-black: hsl(0, 0%, 0%);
  --color-white: hsl(360, 100%, 100%);

  /* BRAND COLORS */
  --color-brand-1: hsl(0, 0%, 96%);
  --color-brand-2: hsl(203, 61%, 22%);
  --color-brand-3: hsl(203, 61%, 20%);
  --color-brand-4: hsl(203, 61%, 15%);
  --color-brand-5: hsl(203, 61%, 11%);
  --color-brand-6: hsl(203, 61%, 8%);

  /* GRAYS */
  --color-gray-1: hsl(0, 0%, 98%);
  --color-gray-2: hsl(0, 15%, 90%);
  --color-gray-3: hsl(0, 0%, 60%);
  --color-gray-4: hsl(0, 0%, 30%);
  --color-gray-5: hsl(0, 0%, 11%);

  /* MISC */
  --color-link: hsl(195, 100%, 33%);

  /* FONT FAMILIES */
  --font-mukta: 'Mukta', sans-serif;
  --font-noto-sans: 'Noto Sans', sans-serif;

  /* FONT WEIGHTS */
  --weight-regular: 400;
  --weight-semi-bold: 600;
  --weight-extra-bold: 800;

  /* GLOBAL MEASUREMENTS */
  --dy-gutter: 16px;
  --dy-button-border-focus-radius: 6px;
  --dy-button-border-radius: 4px;

  /* TRANSITION */
  --global-transition: .3s ease;
}